header {
    height: 150vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

.title {
    padding-bottom: 1rem;
    font-size: 1rem;
}

.title h1{
    font-size: 1.5rem;
}

/* ================= CTA ================= */

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* ================= HEADER SOCIALS ================= */

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    position: absolute;
    left: 18rem;
    bottom: 3rem;
    transform: rotate(0deg);
}

.header__socials a{
    transform: rotate(0deg);
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
}

/* ================= ME ================= */

.me {
    background: linear-gradient(#FF69B4, transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/* =================== MEDIA QUERIES (MEDIUM DEVICES) ====================== */

@media screen and (max-width: 1024px) {
    header {
        height: 136vh;
    }
}

/* =================== MEDIA QUERIES (SMALL DEVICES) ====================== */

@media screen and (max-width: 600px) {
    header {
        height: 150vh;
    }

    .header__socials, 
    .scroll__down {
        display: none;
    }
}